import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UserService } from 'UserService';
import { BattleshipComponent } from './battleship/battleship.component';
import { LandingPageComponent } from './landing-page/landing-page.component';
import { UnityComponent } from './unity/unity.component';
import { WeatherLookupComponent } from './weather-lookup/weather-lookup.component';

const routes: Routes = [
  { path: 'landing-page', component: LandingPageComponent },
  { path: '', redirectTo: '/landing-page', pathMatch: 'full' },
  //{ path: 'unity', component: UnityComponent, canActivate:[UserService] },
  { path: 'unity', component: UnityComponent, canActivate: [UserService] },
  { path: 'weather', component: WeatherLookupComponent, canActivate: [UserService] },
  { path: 'battleship', component: BattleshipComponent, canActivate: [UserService] }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
