import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';

@Component({
  selector: 'unity',
  templateUrl: './unity.component.html',
  styleUrls: ['./unity.component.scss']
})
export class UnityComponent implements OnInit, OnDestroy {

  gameInstance: any;
  progress = 0;
  isReady = false;

  constructor() { }

  ngOnDestroy(): void {
    if(this.gameInstance == null) return;
    this.gameInstance.Quit(function() {
      console.log("done!");
    });
    this.gameInstance = null;
  }

  ngOnInit(): void {
    
    var loader = (window as any).UnityLoader;
    this.gameInstance = loader.instantiate(
    'gameContainer',
    '/assets/Build/Build.json', {
    onProgress: (gameInstance: any, progress: number) => {
        this.progress = progress;
        if (progress === 1) {
          this.isReady = true;
        }
      }
    });

    window.addEventListener('keydown',(e) => {
      if('e' == e.key && this.gameInstance != null) {
        this.gameInstance.Quit(function() {
          console.log("done!");
        });
        this.gameInstance = null;
      }
    })
  }
}
